@use "../util" as *;

.registre {
  padding-top: 3rem;
  @include breakpoint-down(extrasmall) {
    padding-top: rem(680);
  }
  @include breakpoint-down(small) {
    padding-top: rem(820);
  }
  @include breakpoint-down(mobile) {
    padding-top: rem(730);
  }

  &_text {
    text-align: center;
    margin: 0 rem(352);
    @include breakpoint-down(medium) {
      margin: 0 rem(176);
    }
    @include breakpoint-down(xsmall) {
      margin: 0 rem(80);
    }
    @include breakpoint-down(small) {
      margin: 0 rem(30);
    }
    h3 {
      font-family: var(--font-stat);
      font-size: rem(48);
    }
    p {
      @include breakpoint-down(small) {
        font-size: rem(12);
      }
    }
  }

  &_blocs {
    margin: rem(16) 0;
    &__contents {
      input {
        display: none;
      }
    }
    &__elts {
      display: flex;
      justify-content: space-evenly;
      label {
        &:hover {
          border: solid rem(0) var(--white);
          border-radius: rem(16);
          background-color: var(--pink);
        }
        h4 {
          color: var(--black);
          text-align: center;
          margin: 0;
          padding: rem(8) rem(15);
          cursor: pointer;
          @include breakpoint-down(extrasmall) {
            font-size: rem(12);
          }
        }
      }
    }
    &__list {
      .elts {
        display: none;
        margin-top: rem(15);
        &-imgs {
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          @include breakpoint-down(small) {
            grid-template-columns: repeat(3, 1fr);
          }
        }
        &-img {
          position: relative;
          &:hover > .elts-desc {
            transition: all 0.5s ease-in-out;
            opacity: 1;
          }

          img {
            width: 100%;
          }
        }
        &-desc {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          // font-size: rem(23);
          color: var(--pink);
          background-color: var(--white);
          // padding: rem(4) rem(15);
          // border-radius: rem(4);
          opacity: 0;
          transition: all 0.7s ease-in-out;
        }
      }
    }
  }

  #other:checked ~ .registre_blocs__list .other,
  #clothes:checked ~ .registre_blocs__list .clothes,
  #cuisine:checked ~ .registre_blocs__list .cuisine {
    display: block;
  }

  #other:checked ~ .registre_blocs__list .cuisine,
  #clothes:checked ~ .pregistre_blocs__list .cuisine {
    display: none;
  }
}
.registre_copy {
  text-align: center;
  font-size: rem(15);
  font-weight: 400;
}
